export const isBrowser = () => typeof window !== 'undefined';

export const getTheme = () =>
  isBrowser() && localStorage.getItem('theme')
    ? JSON.parse(localStorage.getItem('theme'))
    : 'light';

const setTheme = theme => localStorage.setItem('theme', JSON.stringify(theme));

export const getNavDockedStatus = () =>
  isBrowser() && localStorage.getItem('navIsDocked')
    ? JSON.parse(localStorage.getItem('navIsDocked'))
    : 'false';

const setNavDockedStatus = navIsDocked =>
  localStorage.setItem('navIsDocked', JSON.stringify(navIsDocked));
